.header__logo {
    width: 142px;
    height: 33px;
    object-fit: cover;
    padding: 0 0 40px 0;
}

@media screen and (max-width: 934px) {
    .header__logo {
        margin: 0 0 0 27px;
    }
}

@media screen and (max-width: 767px) {
    .header__logo {
        width: 103.74px;
        height: 24.4px;
        padding: 0 0 31.6px 0;
    }
}